import { createStore } from 'vuex'
import axios from 'axios'
import router from '@/router'

export default createStore({
  state: {
    serverURL: "https://dev1.med-game.ru/api/v1",
    testId: 11,
    startQuestionId: 563,
    questionId: 563,
    parentId: 0,
    infoText: "",
    questionText: "",
    answerTransitions: [],
    currentTransitionId: 0,
    questionStack: [563]
  },
  getters: {
    testId(state){
      return state.testId
    },
    questionId(state){
      return state.questionId
    },
    // bodyData(state){
    //   return state.bodyData
    // },
    parentId(state){
      return state.parentId
    },
    infoText(state){
      return state.infoText
    },
    questionText(state){
      return state.questionText
    },
    answerTransitions(state){
      return state.answerTransitions
    },
    questionStack(state){
      return state.questionStack
    },
    startQuestionId(state){
      return state.startQuestionId
    },    

  },
  mutations: {
    setDataQuestion(state, data){
      // state.parentId = data.body.parentId
      state.infoText = data.body.infoText
      state.questionText = data.body.questionText
      state.answerTransitions = data.body.answerTransitions
      if (data.body.answerTransitions.length === 1) {
        state.currentTransitionId = data.body.answerTransitions[0].transitionId
      }
      else{
        state.currentTransitionId = 0
      }
    },
    setQuestionId(state, questionId){
      state.questionId = questionId
    },
    setCurrentTransitionId(state, transitionId){
      state.currentTransitionId = transitionId
    },
    addToStack(state, id){
      state.questionStack.push(id)
    },
    removeFromStack(state){
      state.questionStack.pop()
      state.questionId = state.questionStack[state.questionStack.length - 1]
    },
    addToLocalStorage(state){
      localStorage.setItem(`questionList${state.testId}`, JSON.stringify(state.questionStack))
    },
    continueTest(state, questionList){
      state.questionStack = questionList
      state.questionId = questionList[questionList.length - 1]
    },
    newTest(state){
      state.questionStack = [state.startQuestionId]
      state.questionId = state.startQuestionId
      router.push('/')
    }

  },

  actions: {

    GET_QUESTION_DATA({state, commit}){
      axios({
        method: 'get',
        url: `${state.serverURL}/question/${state.questionId}`,
      })
      .then((questionData) => {
        commit("setDataQuestion", questionData.data.data)
        // console.log(questionData);
      })        
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
        return error
      })
    },

    nextQuestion({commit, dispatch, state}){
      if (state.currentTransitionId) {
        commit('addToStack', state.currentTransitionId)
        commit('addToLocalStorage')
        commit('setQuestionId', state.currentTransitionId)        
        return dispatch('GET_QUESTION_DATA')
      }
      else {
        console.log("Не выбран ответ")
      }
    },
    previousQuestion({commit, dispatch}){
      commit('removeFromStack')
      commit('addToLocalStorage')
      return dispatch('GET_QUESTION_DATA')
    },

    setCurrentTransitionId({commit}, transitionId){
      commit("setCurrentTransitionId", transitionId)
    },

    continueTest({commit, dispatch}, questionList){
      commit("continueTest", questionList)
      return dispatch('GET_QUESTION_DATA')
    },
    newTest({commit, dispatch}){
      commit("newTest")
      commit('addToLocalStorage')
      return dispatch('GET_QUESTION_DATA')
    }

  },
  modules: {

  }
})
